    --aquamarine: darkcyan;
}

* {
    margin: 0;
    padding: 0; 
    box-sizing: border-box;
}

body {
    font-family: 'Roboto';
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.flex-row-container {
    display: flex;
    flex-direction: row;
}

input, textarea, select  {
    font-family: inherit;
    border: 0.1px solid #ccc;
}

header {
    font-family: 'Quicksand';
    display: flex;
    justify-content: center;
    padding: 10px;
    background-color: var(--aquamarine);
    color: white;
}

.empty-todos {
    color: red;
    font-size: 1.5rem;
}

.empty-projects {
    color: red;
    font-size: 1.1rem;
}

.btn-primary,
.btn-secondary {
    padding: 10px;
    border-style: none;
    border-radius: 5px;
    font-family: 'Quicksand';
    font-weight: 700;
    font-size: 1.1rem;
}

.btn-primary {
    background-color: var(--aquamarine);
    color: whitesmoke;
}

.btn-secondary {
    background-color: whitesmoke;
    color: var(--aquamarine);
    border: 1px solid var(--aquamarine);
}

.btn-primary:hover,
.btn-secondary:hover {
    transform: scale(1.25);
}

.dialog-form {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 400px;
    min-height: 200px;
    border: none;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    text-align: center;
}

.form-enter-data {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 30px 0px;
    flex: 0;
}

.dialog-form::backdrop {
    background: rgba(0, 0, 0, 0.5);
}

.form-enter-data input, textarea {
    border-radius: 2px;
}

.form-enter-data textarea {
    min-height: 200px;
    min-width: 500px;
}

.form-enter-data .btn-primary {
    align-self: center;
}

#content {
    display: flex;
    flex-direction: row;
    flex: 1;
}

.sidebar {
    max-width: 25%;
    width: 25%;
    padding: 20px 30px;
    background-color: aliceblue;
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    align-content: center;
    text-align: center;
    gap: 20px;
}

.sidebar-section {
    width: 100%;
}

.sidebar-header {
    margin: 10px 0px;
    font-size: 1.25rem;
    font-weight: 500;
    color: var(--aquamarine);
}

.project-sidebar {
    display: flex;
    justify-content: space-between;
    padding: 5px;
    font-weight: 500;
    text-align: left;
}

.project-sidebar:hover {
    background-color: lightblue;
    opacity: 0.8;
    transform: scale(1.1);
}

.project-name {
    flex: 1;
    align-self: center  ;
} 

.project-btn-container {
    display: flex;
    flex-direction: row;
    gap: 5px;
}

.btn-icon:hover {
    opacity: 0.5;
}

#todos {
    padding: 10px 30px;
}

.todo {
    padding: 10px;
}

.todo:hover {
    transform: scale(1.1);
    border-bottom: 1px solid var(--aquamarine);
    border-left: 1px solid var(--aquamarine);
}

.details-container {
    display: flex;
    flex-direction: column;
    align-items: start;
}

.title {
    font-size: 1.5rem;
    font-weight: 500;
}

.due-date {
    margin: 10px 0px;
    border-radius: 5px;
    font-family: 'Quicksand';
    font-size: 0.8rem;
    font-weight: 600;
    background-color: var(--aquamarine);
    color: whitesmoke;
    padding: 7px;
}

.todo-btn-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 30px;
    align-items: center;
    margin: 10px;
}

.check-mark-container:hover {
    opacity: 0.5;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAGA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,qBAAqB;IACrB,aAAa;IACb,sBAAsB;IACtB,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,oBAAoB;IACpB,wBAAwB;AAC5B;;AAEA;IACI,wBAAwB;IACxB,aAAa;IACb,uBAAuB;IACvB,aAAa;IACb,mCAAmC;IACnC,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,iBAAiB;AACrB;;AAEA;IACI,UAAU;IACV,iBAAiB;AACrB;;AAEA;;IAEI,aAAa;IACb,kBAAkB;IAClB,kBAAkB;IAClB,wBAAwB;IACxB,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,mCAAmC;IACnC,iBAAiB;AACrB;;AAEA;IACI,4BAA4B;IAC5B,wBAAwB;IACxB,mCAAmC;AACvC;;AAEA;;IAEI,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,gBAAgB;IAChB,iBAAiB;IACjB,YAAY;IACZ,mBAAmB;IACnB,aAAa;IACb,yCAAyC;IACzC,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,gBAAgB;IAChB,OAAO;AACX;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,OAAO;AACX;;AAEA;IACI,cAAc;IACd,UAAU;IACV,kBAAkB;IAClB,2BAA2B;IAC3B,aAAa;IACb,OAAO;IACP,sBAAsB;IACtB,mBAAmB;IACnB,qBAAqB;IACrB,kBAAkB;IAClB,SAAS;AACb;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,gBAAgB;IAChB,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,YAAY;IACZ,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,2BAA2B;IAC3B,YAAY;IACZ,qBAAqB;AACzB;;AAEA;IACI,OAAO;IACP,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,qBAAqB;IACrB,0CAA0C;IAC1C,wCAAwC;AAC5C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,wBAAwB;IACxB,iBAAiB;IACjB,gBAAgB;IAChB,mCAAmC;IACnC,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,6BAA6B;IAC7B,WAAW;IACX,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1